interface BackendErrorOverlay {
  style: "backendError";
  errorMessage: string;
  traceId: string;
  traceIdLogLink: string;
  environmentSlug: string;
  environmentId: string;
}

interface ViteFrontendOverlay {
  style: "viteFrontend";
}

interface RR7Overlay {
  style: "rr7Frontend";
  errorMessage: string;
  stackTrace?: string;
  environmentSlug: string;
}

type OverlayStyle = BackendErrorOverlay | ViteFrontendOverlay | RR7Overlay;

const baseStyle = /* css */ `

.window {
  font-family: var(--inconsolata);
  line-height: 1.5;
  max-width: 80vw;
  box-sizing: border-box;
  margin: 30px auto;
  position: relative;
  border-radius: 6px 6px 8px 8px;
  box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
  overflow: hidden;
  direction: ltr;
  text-align: left;
  outline: 1px #db3927 solid;
  outline-offset: -1px;
}

.error-overlay {
  font-family: var(--inconsolata);
  width: 100%;
  background: white;
  overflow: hidden;
  border-radius: 8px;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 16px;
  display: inline-flex;
}

.error-container {
  align-self: stretch;
  background: white;
  overflow: hidden;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  display: flex;
}

.error-content {
  align-self: stretch;
  padding: 12px;
  overflow: hidden;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 12px;
  display: flex;
}

.error-header {
  align-self: stretch;
  position: relative;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 10px;
  display: flex;
}

.error-title-row {
  align-self: stretch;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 8px;
  display: inline-flex;
}

.error-icon-wrapper {
  padding-top: 2px;
  padding-bottom: 2px;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  display: flex;
}

.error-title pre {
  margin: 0;
  white-space: pre-wrap;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  color: inherit;
}

.error-icon {
  width: 16px;
  height: 16px;
  position: relative;
  overflow: hidden;
}

.error-details {
  align-self: stretch;
  background: #f6f6f6;
  border-radius: 8px;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  display: flex;
}

.error-details-content {
  align-self: stretch;
  padding: 8px 12px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.error-file-path {
  align-self: stretch;
  color: #2d53b1;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  word-wrap: break-word;
  cursor: pointer;
}

.error-file-path:hover {
  text-decoration: underline;
}

.error-message {
  align-self: stretch;
  color: black;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  word-wrap: break-word;
}

.error-stack {
  align-self: stretch;
  padding: 8px 12px;
  overflow: hidden;
  border-radius: 8px;
  outline: 1px #cbcbcb solid;
  outline-offset: -1px;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 12px;
  display: inline-flex;
}

.error-stack-content {
  flex: 1 1 0;
  align-self: stretch;
  color: #545454;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  word-wrap: break-word;
}

.error-actions {
  align-self: stretch;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 12px;
  display: flex;
}

.error-action-row {
  justify-content: center;
  align-items: center;
  gap: 12px;
  display: inline-flex;
}

.error-fix-button {
  border-radius: var(--radius-button-radius, 6px);
  border: 1px solid #de97f3;
  background: #fff;
  box-shadow: 0px -1px 0px 0px #cb4dc8 inset, 0px 1px 1px 0px rgba(0, 0, 0, 0.16);
  display: flex;
  padding: var(--scale-scale200, 6px) 12px;
  justify-content: center;
  align-items: center;
  gap: var(--scale-scale300, 8px);
  cursor: pointer;
}

.error-fix-button:hover {
  background-color: #f6f6f6;
}

.error-fix-button:active:not(:disabled) > * {
  transform: translateY(1px);
}

.error-fix-icon {
  width: 16px;
  height: 16px;
  position: relative;
  overflow: hidden;
}

.error-fix-icon-gradient {
  width: 12.94px;
  height: 13.34px;
  left: 1.45px;
  top: 1.6px;
  position: absolute;
  background: radial-gradient(ellipse 71.93% 69.94% at 0.36% 47.95%, #a259ff 1%, #ff597d 100%);
}

.error-fix-text {
  color: #9b2e84;
  font-size: 14px;
  font-family: var(--inconsolata);
  font-weight: 500;
  line-height: 20px;
  word-wrap: break-word;
}

.message {
  white-space: pre-wrap;
}

.error-title pre {
  margin: 0;
  white-space: pre-wrap;
  flex: 1 1 0;
  justify-content: center;
  display: flex;
  flex-direction: column;
  color: #b13325;
  font-size: 14px;
  font-family: var(--inconsolata);
  font-weight: 700;
  line-height: 20px;
  word-wrap: break-word;
}

.error-trace-id-content-box {
  width: 100%;
  box-sizing: border-box;
  padding-left: 12px;
  padding-right: 12px;
  padding-top: 8px;
  padding-bottom: 8px;
  border-radius: 6px;
  outline: 1px var(--gray-alpha-alpha300, rgba(0, 0, 0, 0.20)) solid;
  outline-offset: -1px;
  justify-content: space-between;
  align-items: center;
  display: inline-flex;
  cursor: pointer;
}

.error-trace-id-content-box:hover {
  background-color: var(--gray-alpha-alpha100, rgb(241, 241, 241));
}

.error-trace-id-content-box:active:not(:disabled) > * {
  transform: translateY(1px);
}

.error-trace-id-content {
  flex: 1 1 0;
  justify-content: flex-start;
  align-items: center;
  gap: 6px;
  display: flex;
}

.error-stack-trace-content-box {
  width: 100%;
  box-sizing: border-box;
  height: 100%;
  padding-left: 12px;
  padding-right: 12px;
  padding-top: 8px;
  padding-bottom: 8px;
  overflow: hidden;
  border-radius: 8px;
  outline: 1px var(--gray-gray300, #CBCBCB) solid;
  outline-offset: -1px;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 12px;
  display: inline-flex;
}
`;

export const Style = (overlayStyle: Pick<OverlayStyle, "style">): string => {
  const style: string[] = [];

  switch (overlayStyle.style) {
    case "backendError":
    case "rr7Frontend":
      style.push(`
body {
  background: #515151;
}`);
      break;
    case "viteFrontend":
      style.push(`
:host {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 99999;
  --inconsolata: Gadget-Inconsolata-Private, sans-serif;
      background: #515151;
}`);
      break;
  }

  return InconsolataFont + style.join("\n") + baseStyle;
};

const getErrorMessage = (overlayStyle: OverlayStyle) => {
  switch (overlayStyle.style) {
    case "backendError": {
      return `${overlayStyle.errorMessage}\n\nTrace ID: ${overlayStyle.traceId}`;
    }
    case "rr7Frontend": {
      return `${overlayStyle.stackTrace}`;
    }
    default: {
      return ``;
    }
  }
};

export const overlayTemplate = (overlayStyle: OverlayStyle) => {
  return /* html */ `
<style>
  ${Style(overlayStyle)}
</style>

<div class="window">
<div class="error-overlay">
  <div class="error-container">
    <div class="error-content">
      <div class="error-header">
        <div class="error-title-row">
          <div class="error-icon-wrapper">
            <div class="error-icon">
            <img src="${ErrorIconUrl}" alt="Error icon" width="16" height="16" />
            </div>
          </div>
          <div class="error-title">
            <pre id="plugin-message">${overlayStyle.style !== "viteFrontend" ? overlayStyle.errorMessage : ""}</pre>
          </div>
        </div>
      </div>
      ${
        overlayStyle.style === "backendError"
          ? /* html */ `
          <div class="error-trace-id-content-box">
              <div class="error-trace-id-content">
                  <div style="color: var(--gray-alpha-alpha600, rgba(0, 0, 0, 0.67)); font-size: 12px; font-weight: 500; line-height: 17px; word-wrap: break-word">traceID: ${overlayStyle.traceId}</div>
              </div>
              <img src="${ChevronRightIconUrl}" alt="Chevron right icon" width="16" height="16" />
          </div>
        `
          : overlayStyle.style === "viteFrontend"
          ? /* html */ `
      <div class="error-details-content">
        <div id="file" class="error-file-path"></div>
      </div>
      `
          : overlayStyle.style === "rr7Frontend"
          ? `
          <div class="error-stack-trace-content-box">
            <div style="flex: 1 1 0; align-self: stretch; color: var(--gray-gray600, #545454); font-size: 12px; font-weight: 500; line-height: 16px; word-wrap: break-word">${overlayStyle.stackTrace}</div>
          </div>
          `
          : ""
      }

      <div class="error-actions">
        <div class="error-action-row">
        <button class="error-fix-button">
        <img src="${FixIconUrl}" alt="Fix icon" width="16" height="16" />
            <div class="error-fix-text">Fix error</div>
          </button>
        </div>
      </div>
    </div>
  </div>
</div>
</div>

${
  overlayStyle.style !== "viteFrontend"
    ? `
<script defer suppressHydrationWarning>
  console.log("[Gadget-Error-Overlay] Rendering error overlay");

  const errorMessage = \`${getErrorMessage(overlayStyle)}\`;
  const fixErrorLink = \`\${window.location.origin}/edit/${overlayStyle.environmentSlug}?prompt=${encodeURIComponent(
        "Can you fix the following error?\n\n" + getErrorMessage(overlayStyle)
      ).slice(0, 10000)}\`;
  const inIFrame = window.self !== window.top;

  const fixButton = document.querySelector(".error-fix-button");
  if (fixButton) {
    console.log("[Gadget-Error-Overlay] Fix button found attaching event listener");
    fixButton.addEventListener("click", () => {
      console.log("[Gadget-Error-Overlay] Fix button clicked");
      if (inIFrame) {
        console.log("[Gadget-Error-Overlay] Sending fix error message to parent");
        window.parent.postMessage({ type: "gadget:fixError", error: { errorMessage } }, "*");
      } else {
        console.log("[Gadget-Error-Overlay] Opening fix error link");
        window.open(fixErrorLink, "_blank");
      }
    });
  } else {
    console.error("[Gadget-Error-Overlay] Fix button not found");
  }

  ${
    overlayStyle.style === "backendError"
      ? /* html */ `
  const traceIdButton = document.querySelector(".error-trace-id-content-box");
  if (traceIdButton) {
    traceIdButton.addEventListener("click", () => {
      if (inIFrame) {
        console.log("[Gadget-Error-Overlay] Sending open logs message to parent");
        window.parent.postMessage({ type: "gadget:openLogs", traceId: "${overlayStyle.traceId}" }, "*");
      } else {
        console.log("[Gadget-Error-Overlay] Opening trace ID log link");
        window.open(\`${overlayStyle.traceIdLogLink}\`, "_blank");
      }
    });
  } else {
    console.error("[Gadget-Error-Overlay] Trace ID button not found");
  }
  `
      : ""
  }
</script>`
    : ""
}
`;
};

declare const currentEnvironment: string;
// Make HTMLElement available in non-browser environments
const { HTMLElement = class {} as typeof globalThis.HTMLElement } = globalThis;
class ErrorOverlay extends HTMLElement {
  root: ShadowRoot;

  constructor(err: any) {
    super();
    this.root = this.attachShadow({ mode: "open" });
    this.root.innerHTML = overlayTemplate({ style: "viteFrontend" });
    this.dir = "ltr";

    this.text("#plugin-message", `[plugin:${err.plugin}] ${err.message.trim()}`);

    const [file] = (err.loc?.file || err.id || "unknown").split(`?`);
    let fileText;
    if (err.loc) {
      fileText = `${file}:${err.loc?.line}:${err.loc?.column}`;
    } else if (err.id) {
      fileText = file;
    } else {
      fileText = "unknown";
    }

    let fileLink: HTMLDivElement | null = null;
    const gadgetFolderMatch = file.match(/\/(web|api)\/(.+)/);
    if (gadgetFolderMatch) {
      const filePathForLink = gadgetFolderMatch ? `${gadgetFolderMatch[1]}/${gadgetFolderMatch[2]}` : file;
      if (window.self !== window.top) {
        const filePathButton = this.root.querySelector(".error-file-path");
        if (filePathButton) {
          filePathButton.addEventListener("click", () => {
            window.parent.postMessage({ type: "gadget:openFile", filePath: filePathForLink }, "*");
          });
        }
      } else {
        fileLink = this.createLink(`${fileText}`, `${window.location.origin}/edit/${currentEnvironment}/files/${filePathForLink}`);
      }
    }

    if (fileLink) {
      this.text("#file", fileLink.innerHTML, true);
    } else {
      this.text("#file", fileText);
    }

    const fixButton = this.root.querySelector(".error-fix-button");
    if (fixButton) {
      fixButton.addEventListener("click", () => {
        if (window.self !== window.top) {
          window.parent.postMessage({ type: "gadget:fixError", error: { errorMessage: err.message } }, "*");
        } else {
          window.open(
            `${window.location.origin}/edit?prompt=${encodeURIComponent(`Can you fix the following error?\n\n${err.message}`)}`,
            "_blank"
          );
        }
      });
    } else {
      console.error("fix button not found");
    }
  }

  text(selector: string, text: string | undefined, html = false): void {
    if (!text) {
      return;
    }

    const el = this.root.querySelector(selector);

    if (!el) {
      return;
    }

    if (html) {
      // Automatically detect links
      text = text
        .split(" ")
        .map((v) => {
          if (!v.startsWith("https://")) return v;
          if (v.endsWith(".")) return `<a target="_blank" href="${v.slice(0, -1)}">${v.slice(0, -1)}</a>.`;
          return `<a target="_blank" href="${v}">${v}</a>`;
        })
        .join(" ");

      el.innerHTML = text.trim();
    } else {
      el.textContent = text.trim();
    }
  }

  createLink(text: string, href: string | undefined): HTMLDivElement {
    const linkContainer = document.createElement("div");
    const linkElement = href ? document.createElement("a") : document.createElement("button");
    linkElement.innerHTML = text;

    if (href && linkElement instanceof HTMLAnchorElement) {
      linkElement.href = href;
      linkElement.target = "_blank";
    }

    linkContainer.appendChild(linkElement);
    linkContainer.className = "link";

    return linkContainer;
  }

  close(): void {
    this.parentNode?.removeChild(this);
  }
}

function getOverlayCode(environment: string) {
  return `
    const baseStyle = \`${baseStyle}\`;
    const ChevronRightIconUrl = "${ChevronRightIconUrl}";
    const ErrorIconUrl = "${ErrorIconUrl}";
    const FixIconUrl = "${FixIconUrl}";
    const InconsolataFont = \`${InconsolataFont}\`;
    const getErrorMessage = ${getErrorMessage};
    const Style = ${Style};
		const overlayTemplate = ${overlayTemplate};
    const currentEnvironment = "${environment}";
		${ErrorOverlay.toString()}
	`;
}

export function patchOverlay(code: string, environment: string): string {
  let result = code.replace("class ErrorOverlay", getOverlayCode(environment) + "\nclass ViteErrorOverlay");
  result = result.replace('_define_property()._(this, "root", void 0);', "");

  return result;
}
